<template>
  <div class="calculator">
    <div class="screen">
      <input type="text" v-model="result" readonly>
    </div>
    <div class="buttons">
      <button v-for="button in buttons" :key="button" @click="handleButtonClick(button)">{{ button }}</button>
      <button @click="clear">C</button>
    </div>
  </div>
</template>

<script>
export default {
  name: 'CalculatorApp', // Ganti nama komponen menjadi multi-word
  data() {
    return {
      result: '',
      buttons: ['7', '8', '9', '+', '4', '5', '6', '-', '1', '2', '3', '*', '0', '.', '=', '/']
    };
  },
  methods: {
    handleButtonClick(button) {
      if (button === '=') {
        try {
          this.result = eval(this.result);
        } catch (error) {
          this.result = 'Error';
        }
      } else if (button === 'C') {
        this.clear();
      } else {
        this.result += button;
      }
    },
    clear() {
      this.result = '';
    }
  }
};
</script>

<style>
.calculator {
  width: 200px;
  margin: 0 auto;
  text-align: center;
  font-family: Arial, sans-serif;
}

.screen {
  background-color: #f0f0f0;
  padding: 10px;
  border: 1px solid #ccc;
}

.buttons {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
}

button {
  font-size: 18px;
  padding: 10px;
  border: 1px solid #ccc;
  cursor: pointer;
}

button:hover {
  background-color: #f0f0f0;
}
</style>
